import cuCustom from '@/colorui/components/cu-custom.vue'
export default {
	onLoad() {
		// 进入这个页面的时候创建websocket连接【整个页面随时使用】
		this.getInfoList()
	},
	data() {
		return {
			listTouchStart: 0,
			modalName: null,
			isBold: false,
			baseUrl: 'http://47.104.29.236:9999/api/v1',
			infoList: [],
			socketTask: null,
			is_open_socket: false
		}
	},
	components: {
		cuCustom
	},
	// 关闭websocket【必须在实例销毁之前关闭,否则会是underfined错误】
	beforeDestroy() {
		this.closeSocket()
	},
	methods: {
		// 获取消息列表
		getInfoList() {
			// 创建一个this.socketTask对象【发送、接收、关闭socket都由这个对象操作】
			this.socketTask = uni.connectSocket({
				// 【非常重要】必须确保你的服务器是成功的,如果是手机测试千万别使用ws://127.0.0.1:9099【特别容易犯的错误】
				url: "ws://47.104.29.236:3101",
				success(data) {
					// console.log("websocket连接成功");
				},
			});
			// 用户id
			let user_id = uni.getStorageSync('user_id')
			this.socketTask.onOpen((res) => {
				console.log("WebSocket连接正常打开中...！");
				this.is_open_socket = true;
				// 注：只有连接正常打开中 ，才能正常成功发送消息
				this.socketTask.send({
					data: JSON.stringify({
						user_id: user_id
					}),
					async success() {
						console.log("消息发送成功");
					},
				});
				// 注：只有连接正常打开中 ，才能正常收到消息
				this.socketTask.onMessage((res) => {
					this.infoList = JSON.parse(res.data)
					this.infoList.forEach(v => {
						var date = new Date(v.add_time)
						let hours = date.getHours()
						let seconds = date.getSeconds()
						v.times = hours + ':' + seconds
					})
					// console.log(this.infoList)
				});
			})

			// 这里仅是事件监听【如果socket关闭了会执行】
			this.socketTask.onClose(() => {
				// console.log(this.is_open_socket)
				console.log("已经被关闭了")
			})
		},
		// 关闭websocket【离开这个页面的时候执行关闭】
		closeSocket() {
			this.socketTask.close({
				success: (res) => {
					this.is_open_socket = false
					console.log("关闭成功", res)
				},
				fail: (err) => {
					console.log("关闭失败", err)
				}
			})
		},
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},
		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},
		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		},
		// 跳转聊天页面{
		dump(id, username) {
			// this.$emit('username', username)
			uni.navigateTo({
				url: './chat?id=' + id + '&username=' + username
			})

		},
		// 跳转对应页面
		navgitorTo(url) {
			console.log(url)
			uni.reLaunch({
				url: url
			})
		}
	}
}
